@import '../../scss/variables.scss';

.reasons {
  padding: 0 2rem;
  display: flex;
  gap: 2rem;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  &__leftside {
    flex: 1 1;
    display: grid;
    justify-content: center;
    gap: 1rem;
    grid-auto-rows: 1fr;

    @media (max-width: 768px) {
      grid-auto-rows: auto;
      overflow: hidden;
    }
  }

  &__img {
    object-fit: cover;
  }

  &__image1 {
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;

    @media (max-width: 768px) {
      width: 10rem;
      height: 23rem;
    }
  }
  &__image2 {
    width: auto;
    height: 16rem;
    grid-column: 2/4;
    @media (max-width: 768px) {
      width: 21rem;
      height: 13rem;
    }
  }
  &__image3 {
    width: 13.7rem;
    grid-column: 2/3;
    grid-row: 2;
    @media (max-width: 768px) {
      width: 11rem;
    }
  }
  &__image4 {
    width: 10rem;
    grid-row: 2;
    grid-column: 3/4;
    @media (max-width: 768px) {
      width: 9rem;
      height: 9rem;
    }
  }

  &__rightside {
    flex: 1 1;
    text-transform: uppercase;
    gap: 1rem;
    display: flex;
    flex-direction: column;

    @media (max-width: 768px) {
      align-items: center;
    }
  }

  &__subtitle {
    color: $orange;
    font-weight: bold;
  }

  &__title {
    font-size: 3rem;
    font-weight: bold;
  }
}

.details {
  display: flex;
  flex-direction: column;
  gap: 1rem;

  @media (max-width: 768px) {
    justify-content: center;
    align-self: center;
  }

  &__item {
    display: flex;
    font-size: 1rem;
    gap: 1rem;
    align-items: center;
  }

  &__img {
    width: 2rem;
    height: 2rem;
  }

  &__text {
    color: $gray;
    @media (max-width: 768px) {
      justify-content: center;
      align-self: center;
    }
  }
}

.partners {
  display: flex;
  gap: 1rem;
  @media (max-width: 768px) {
    justify-content: center;
    align-self: center;
    gap: 2rem;
  }

  &__img {
    width: 2.5rem;
  }
}
